.App {
  width: 100vw;
  height: 100vh;
  background: #fff;
  color: #212121;
  font-family: 'Poppins', sans-serif;
  display: grid;
  place-items: center;
}
body {
  margin: 0%;
  padding: 0%;
}
@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

.joinChatContainer {
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 1.5rem;
  border-radius: 5px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
}

.joinChatContainer h3 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}
.joinChatContainer input {
  width: 200px;
  height: 40px;
  margin: 7px;
  border: 1px solid #ddd;
  padding: 4px 10px;
  font-size: 16px;
}

.joinChatContainer input.focus-visible {
  border-color: #02e74e !important;
  outline: none !important;
}

.joinChatContainer button {
  width: 225px;
  height: 50px;
  margin: 7px;
  border: none;
  border-radius: 5px;
  padding: 5px;
  font-size: 16px;
  background: #02e74e;
  color: #000;
  cursor: pointer;
  font-weight: bold;
}

.joinChatContainer button:hover {
  background: #00c742;
}

.chat-window {
  width: 300px;
  height: 420px;
}

.chat-window p {
  margin: 0;
  font-size: 0.75rem;
}
.chat-window .chat-header {
  height: 45px;
  background: #14141b;
  color: white;
  position: relative;
  cursor: pointer;
}
.chat-window .chat-header p {
  display: block;
  padding: 0 1em;
  color: #fff;
  font-weight: 700;
  line-height: 45px;
  font-size: 0.85rem;
}

.chat-window .chat-body {
  height: calc(450px - (45px + 70px));
  border: 1px solid #ddd;
  border-top: none;
  background: #fff;
  position: relative;
}

.chat-body > div > div::-webkit-scrollbar {
  background: #eee;
  width: 4px;
}

.chat-body > div > div::-webkit-scrollbar-thumb {
  background: #aaa;
}

.chat-window .chat-body .message-container {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
}

.chat-window .chat-body .message-container::-webkit-scrollbar {
  display: none;
}
.chat-window .chat-body .message {
  height: auto;
  padding: 10px;
  display: flex;
}

.chat-window .chat-body .message .message-content {
  width: auto;
  height: auto;
  min-height: 40px;
  max-width: 200px;
  background-color: #02e74e;
  border-radius: 3px;
  color: black;
  display: flex;
  align-items: center;
  padding: 10px;
  overflow-wrap: break-word;
  word-break: break-word;
  font-size: 0.85rem;
}

#you {
  justify-content: flex-end;
}

#you .message-content {
  justify-content: center;
  position: relative;
}

#you .message-meta {
  justify-content: flex-end;
  margin-left: 5px;
}

#other {
  justify-content: flex-start;
}

#other .message-content {
  justify-content: flex-start;
  background-color: #e4e3e8;
}

#other .message-meta {
  justify-content: flex-start;
  margin-right: 5px;
}

.message-meta #author {
  margin-left: 5px;
  font-weight: bold;
}

.chat-window .chat-body .message .message-meta {
  display: flex;
  font-size: 12px;
}

.chat-window .chat-footer {
  height: 40px;
  border: 1px solid #ddd;
  border-top: none;
  display: flex;
}

.chat-window .chat-footer input {
  height: 100%;
  flex: 85%;
  border: 0;
  padding: 0 0.7em;
  font-size: 0.85rem;

  outline: none;
  font-family: "Open Sans", sans-serif;
}

.chat-window .chat-footer button {
  border: 0;
  display: grid;
  place-items: center;
  cursor: pointer;
  flex: 15%;
  height: 100%;
  background: transparent;
  outline: none;
  font-size: 16px;
  background-color: #14141b;
  color: white;
}

.chat-window .chat-footer button:hover {
  color: #43a047;
}
.hide {
  opacity: 0 !important;
}

.react-scroll-to-bottom--css-sjuwu-1tj0vk3:after {
  display: none;
}

.message-status {
  width: 6px;
  height: 6px;
  background-color: #02e74e;
  border-radius: 3px;
  position: relative;
  top: 6px;
  margin-right: 3px;
}

#you .message-status {
  background-color: #ddd;
}
